import * as yup from "yup";
export const namesValidation = {
  name: yup
    .string()
    .required(" !")
    .matches(/^[a-z-\s]+$/i, " "),
};

export const emailValidation = {
  email: yup.string().required(" ").email(" email"),
};

export const descriptionValidation = {
  description: yup.string().required(" ").max(2000),
};

export const nameValidation = {
  nickname: yup
    .string()
    .required(" ")
    .matches(/^[a-z-\s]+$/i, " ")
    .min(2, " ")
    .max(30, " "),
};

export const subjectValidation = {
  subject: yup
    .string()
    .required(" ")
    .matches(/^[a-z-\s]+$/i, " ")
    .min(4, "    3 ")
    .max(30, "     30 "),
};
export const phoneValidation = {
  phone: yup
    .string()
    .required(" ")
    .transform((value) => {
      return value.replace(/[^0-9]/g, "");
    })
    .min(11, "  "),
};
export const passwordValidation = {
  password: yup
    .string()
    .required(" ")
    .min(10, "  10 ")
    .max(15, "  15 "),
};
